.video-contact{
    position: absolute;
     top:0px;
     left:0x; 
     width: 100%;
     height: 100%;
     object-fit: cover;
     z-index: -2;
}
.connect-contact {
    min-height: 40vh;
    position: relative;
    padding: 10rem 0 5rem 0;
    margin: 1rem 0;
  }
  .connect-contact::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    
    /* background: rgba(0, 0, 0, 0.6); */
    opacity: 0.7;
    z-index: -1;
  }
  .connect-contact {
    display: grid;
    place-items: center;
  }
  
  .video-banner-contact .section-title-contact {
    margin-bottom: 2rem;
  }
  .video-text-contact {
     width:100%;
     text-transform: inherit;
     
  }
  .nav-links-contact{
    display: none;
    
  }
  @media screen and (min-width: 768px)
  {
    .nav-links-contact{
      display:grid;
      grid-template-columns: repeat(1, 1fr);
      column-gap: 4rem;
      justify-items: center;
      justify-self: end;
    }
  .nav-links-contact a {
      text-transform: capitalize;
      color: var(--clr-primary-5);
      font-weight: bold;
      letter-spacing: var(--spacing);
      transition: var(--transition);
  }
  .nav-links-contact a:hover {
    color:var(--clr-primary-5);
  }
}
  .video-banner-contact {
    
    padding: 2rem 3rem 10rem 3rem;
    text-align: center;
  
    -webkit-clip-path: polygon(
      0% 0%,
      100% 0%,
      100% 75%,
      75% 75%,
      75% 100%,
      50% 75%,
      0% 75%
    );
    clip-path: polygon(
      0% 0%,
      100% 0%,
      100% 75%,
      75% 75%,
      75% 100%,
      50% 75%,
      0% 75%
    );
  }
  .connect-contact {
    -webkit-clip-path: polygon(
      50% 0%,
      100% 10%,
      100% 90%,
      50% 100%,
      0 90%,
      0 10%
    );
    clip-path: polygon(50% 0%, 100% 10%, 100% 90%, 50% 100%, 0 90%, 0 10%);
  }
  .section-title-contact {
   
    text-align: center;
  }

  @media screen and (min-width:500px){
    .video-banner-contact .section-title-contact {
      margin-bottom: 2rem;
      width:100%;
    }
  }
