/*
=============== 
Fonts
===============
*/
@import url("https://fonts.googleapis.com/css?family=Open+Sans|Roboto:400,700&display=swap");

/*
=============== 
Variables
===============
*/

:root {
  /* dark shades of primary color*/
  
  /* lighter shades of primary color */
  --clr-primary-6: hsl(43, 89%, 70%);
  
  --clr-primary-8: hsl(45, 86%, 81%);
  --clr-primary-9: hsl(45, 90%, 88%);
  --clr-primary-1:yellow;
  /* darkest grey - used for headings */

  --clr-social-icons:#ff5722;
  --text-colors:white;
  --clr-servicer-hover:#51C5F1;
  --mainTransition: all 0.3s linear;
}
/*
=============== 
Global Styles
===============
*/

*,
::after,
::before {
  margin: 0;
  
  box-sizing: border-box;
}
body {
  font-family: var(--ff-secondary);
  background: var(--clr-white);
  color: var(--clr-grey-1);
  line-height: 1.5;
  
}
ul {
  list-style-type: none;
}

img:not(.nav-logo) {
  width: 100%;
  display: block;
}

h1,
h2,
h3,
h4 {
  
  text-transform: capitalize;
  line-height: 1.25;
  margin-bottom: 0.75rem;
  font-family: var(--ff-primary);
}

@media screen and (min-width: 800px) {
  h1 {
    font-size: 4rem;
  }
  h2 {
    font-size: 2.5rem;
  }
  h3 {
    font-size: 1.75rem;
  }
  h4 {
    font-size: 1rem;
    margin-bottom: 1.50rem;
  }
  body {
    font-size: 1rem;
  }
  h1,
  h2,
  h3,
  h4 {
    line-height: 1.25;
  }
}
/*  global classes */


/* section */
.text-color{
  color:var(--clr-white);
  letter-spacing: 2px;
}
.size{
  font-size:6rem;
}
.section {
  padding: 5rem 0;
  
  margin-top: 7%;
  letter-spacing: var(--spacing);
}

.section-center {
  width: 90vw;
  margin: 0 auto;
  max-width: 1170px;
  
}
@media screen and (min-width: 992px) {
  .section-center {
    width: 95vw;
  }
}
/* added during recording */

/*
=============== 
Navbar
===============
*/

.underline{
  width:5rem;
  height:0.25rem;
  margin-bottom: 1.25rem;
  background: var(--clr-primary-5);
  margin-left:auto;
  margin-right: auto;
}
.section-title{
  
  /*margin-bottom: 4rem;*/
  text-align: center;
}
.nav-links{
  display: none;
  
}
.logo{
  color:var(--clr-primary-5);
  box-shadow: 0 2px var(--clr-primary-5);
  font-size:3rem;
}
.nav{
  height:5rem;
  padding:1rem;
  display: grid;
  align-items: center;
  transition: var(--transition);
  
}
.nav-center{
  width:100%;
  max-width: 1170px;
  margin:0 auto;
}
.nav-header{
  display: grid;
  grid-template-columns: auto 1fr;
  align-items: center;
}
.nav-btn{
  background:transparent;
  border-color: transparent;
  color:var(--clr-primary-5);
  font-size:2rem;
  cursor:pointer;
  justify-self: end;
}
@media screen and (min-width:768px){
 
  .nav-btn{
    display:none;
  }
  .nav-links{
    display:grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 4rem;
    justify-items: center;
    justify-self: end;
  }
  .nav-links a{
    text-transform: capitalize;
   
    color:var(--clr--white);
    font-weight: bold;
    letter-spacing: var(--spacing);
    transition: var(--transition);
  }
  .nav-links a:hover {
    color:var(--clr-primary-5);
  }
  .nav-center{
    display: grid;
    grid-template-columns: auto 1fr;
    align-items: center;
  }
}
.navbar-fixed{
  position:fixed;
  top:0;
  left:0;
  width:100%;
  background:url('../images/New\ Design.png');
  background-repeat: no-repeat;
  background-position: right;
  z-index: 2;
  box-shadow: var(--light-shadow);
}
/*
=============== 
Sidebar
===============
*/
.sidebar{
  position:fixed;
  top:0;
  left:0;
  width:100%;
  height:100%;
  background:url('../images/New\ Design.png');
  z-index: 4;
  display: grid;
  place-items: center;
  transition: var(--transition);
 transform: translateX(-100%);
}
.show-sidebar{
  transform: translateX(0);
}
.sidebar-links{
  text-align: center;

}
.sidebar-links a{
  color: var(--clr-white);
  font-size:1.9rem;
  text-transform: capitalize;
  transition: var(--transition);
  letter-spacing: var(--spacing);
  display: inline-block;
  margin-bottom: 1.5rem;
}
.sidebar-links a:hover{
  color:orange;
}
.social-icons{
  display: grid;
  margin-top:3rem;
  
  width:18rem;
  grid-template-columns: repeat(3, 1fr);
  
 
}
.social-icon{
  font-size: 1.5rem;
  color:var(--clr-primary-5);
  transition: var(--transition);
  
}
.social-icon:hover{
  color:var(--text-colors);
}
.close-btn{
  position: absolute;
  top:1rem;
  right:1rem;
  font-size: 2rem;
  background:transparnte;
  border:transparent;
  transition: var(--transition);
  color:var(--clr-primary-5);
  cursor: pointer;
}
.close-btn:hover{
  color:#e66b6b;
}
.hero .underline{
  margin-bottom: 0.5rem;
  margin-left:3%;
}
.hero-img{
  display: none;
}
.hero-info{
  text-align: center;
}
.hero-center{
  min-height: calc(100vh - 5rem);
  display: grid;
  place-items: center;
}
.bg-grey{
  background: url('../images/conn.png');
  background-position: right;
  background-repeat: no-repeat;
  object-fit: cover;
  min-height: 100vh;
}

.btn {
  display: inline-block;
  padding: 0.8rem 1.9rem;
  text-transform: capitalize;
  letter-spacing: 0.1rem;
  background: white;
  color: var(--mainBlack);
  border-radius: 60px;
  transition: var(--mainTransition);
    font-weight: bold;
    font-family: "Roboto";
}
.btn:hover {
  background: var(--text-colors);
  color: var(--clr-primary-5);
}
.hero-btn{
  margin-top:1.25rem;
  
}


@media screen and (min-width:992px){
  .hero-img{
    display: block;
    position:relative;
    bottom:2rem;
  }
  
  .hero-center{
    grid-template-columns: 1fr 1fr;
  }
 
  .hero-img::before,
  .about-img::before{
    content:'';
    position:absolute;
    width:100%;
    height:100%;
    border:0.25rem solid var(--clr-primary-5);
    top:2rem;
    right:-2rem;
    border-radius: var(--radius);
  }
 
}
.hero-img::before,
.about-imgs::before{
  content:'';
  position:absolute;
  width:100%;
  height:100%;
  border:0.25rem solid var(--clr-primary-5);
  top:2rem;
  right:-2rem;
  border-radius: var(--radius);
}
.hero-photo{
  max-width: 16rem;
  max-height: 30rem;
  object-fit: cover;
  border-radius: var(--radius);
  position:relative;
}
@media screen and (min-width:992px){
  .about-center{
    grid-template-columns: 1fr 1fr;
  }
  .about-img{
    position:relative;
  }
  .about-img::before{
    left:-2rem;
  }
  .about-info{
    align-self: center;
  }
}
/*
=============== 
Hero
===============
*/
/* underline added to globals */
.service{
  
  padding:3rem 1.5rem;
  margin-bottom:2rem;
  border-radius: var(--radius);
  text-align: center;
  transition: var(--transition);
  /*background:var(--clr-servicer-hover);*/
  
}
.service-icon{
  font-size: 2rem;
  margin-bottom: 1.5rem;
}
.service .underline{
  width:3rem;
  height:0.12rem;
  transition: var(--transition);
}
.service p{
  transition: var(--transition);
}
.service:hover{
  background:var(--clr-servicer-hover);
  color:var(--clr-white);
  cursor:pointer;
  opacity: 0.8;
}
.service:hover p {
  color:var(--clr-white);
}
.service:hover .underline{
  background-color: var(--clr-white);
}
@media screen and (min-width:676px){
  .services-center{
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 2rem;
  }
}
@media screen and (min-width:992px){
  .services-center{
    
    grid-template-columns: 1fr 1fr 1fr;
    
  }
}

.projects-text{
  width:85vw;
  max-width: 30rem;
  line-height: inherit;
  text-transform: capitalize;
}
@media screen and (min-width:350px){
  .projects-text{
    margin:30px auto;
  }
}
.project{
  position: relative;
  background:#45E3E3;
  margin-bottom: 2rem;
  border-radius: var(--radius);

}

.project-info{
  text-align: center;
  color:var(--clr-white);
  position:absolute;
  top:50%;
  left:50%;
  transform: translate(-50%, -50%);
  transition: var(--transition);
 opacity: 0;
}
.project-info p{
  text-transform: capitalize;
  color:var(--clr-white);
}
.project:hover .project-info{
  opacity:1;
}
.project-img{
  transition: var(--transition);
  border-radius: var(--radius);
  height:15rem;
  object-fit: cover;
}
.project:hover .project-img{
  opacity: 0.1;
}
.project::after{
  content:'';
  position:absolute;
  top:0;
  left:0;
  width: 100%;
  height:100%;
  border:0.25rem solid var(--clr-white);
  border-radius: var(--radius);
  transition: var(--transition);
  opacity: 0;
}
.project:hover::after{
  opacity:1;
  transform: scale(0.8);
}
@media screen and (min-width:676px){
  .projects-center{
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 2rem;
  }
}
  @media screen and (min-width: 992px){
    .projects-center{
      grid-template-columns: repeat(3, 1fr);
      
    }
  }
    @media screen and (min-width: 1170px){
      .projects-center{
  grid-template-rows: 200px 200px; 
  gap:1rem;
  padding: 61px;
  grid-template-areas: 

        "a b b"
        "a c d"    
      }
      .project-img{
        height:100%
      }
      .project{
        height:100%
      }
    
      .project-1{
        grid-area: a;
        height:100%;
      }
      .project-2{
        grid-area: b;
      }
      .project-3{
        grid-area: c;
      }
      .project-4{
        grid-area: d;
      }
}
