}
.nav{
  height:5rem;
  padding:1rem;
  display: grid;
  align-items: center;
  transition: var(--transition);
  
}
.nav-center{
  width:100%;
  max-width: 1170px;
  margin:0 auto;
}
.nav-header{
  display: grid;
  grid-template-columns: auto 1fr;
  align-items: center;
}
.nav-btn{
  background:transparent;
  border-color: transparent;
  color:var(--clr-primary-5);
  font-size:2rem;
  cursor:pointer;
  justify-self: end;
}
@media screen and (min-width:768px){
 
  .nav-btn{
    display:none;
  }
  .nav-links{
    display:grid;
    grid-template-columns: repeat(3, 1fr);
    grid-column-gap: 4rem;
    -webkit-column-gap: 4rem;
            column-gap: 4rem;
    justify-items: center;
    justify-self: end;
  }
  .nav-links a{
    text-transform: capitalize;
   
    color:var(--clr--white);
    font-weight: bold;
    letter-spacing: var(--spacing);
