:root {
  /* dark shades of primary color*/
 
  --clr-primary-2: rgb(231, 66, 94);
  --clr-primary-3: #1d1c19;
  
  --clr-primary-4: hsl(42, 63%, 48%);
  /* primary/main color */
  

  --clr-white: #fff;
  --clr-black:#1d1c19;
  --ff-primary: "Roboto", sans-serif;
  --ff-secondary: "Open Sans", sans-serif;
  
  --light-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  --dark-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
  --max-width: 1170px;
}
/*
=============== 
Global Styles
===============
*/

.tile{
  height:300px;
  object-fit: cover;
}
.tile-nail{
  height:300px;
  object-fit: cover;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
  box-shadow: var(--dark-shadow);
}

@media screen and (min-width: 800px) {
  h1 {
    font-size: 3rem;
  }
  h2 {
    font-size: 2.5rem;
  }
  h3 {
    font-size: 1.75rem;
  }
  h4 {
    font-size: 1.1rem;
  }
 
  h1,
  h2,
  h3{
    line-height: 1;
  }
  
}
/*  global classes */

.btn-nails {
