Navbar
===============
*/
.nav-links-nails {
  display: none;
}

.nav-nails {
  height: 5rem;
  padding: 1rem;
  display: grid;
  align-items: center;
  transition: var(--transition);
}
.nav-center-nails {
  width: 100%;
  max-width: 1170px;
  margin: 0 auto;
}
.nav-header-nails {
  display: grid;
  grid-template-columns: auto 1fr;
  -webkit-box-align: center;
  grid-template-rows: 64px;
  align-items: center;
}
.nav-btn-nails {
  background: transparent;
  border-color: transparent;
  color: var(--clr-primary-5);
  font-size: 2rem;
  cursor: pointer;
  justify-self: end;
}
@media screen and (min-width: 768px) {
  .nav-nails {
    opacity: 0.9;
    width:80%;
    margin:0 auto;
    background: hsl(0deg 100% 85%);
  }
  .nav-btn-nails {
    display: none;
  }
  
  .nav-links-nails {
    display: grid;
    grid-template-columns: repeat(4, 93px);
    justify-items: center;
    -webkit-column-gap: 2rem;
    -moz-column-gap: 2rem;
    grid-column-gap: 1rem;
    -webkit-column-gap: 1rem;
            column-gap: 1rem;
    /* grid-template-rows: 40px; */
    justify-self: end;
  }
  .nav-links-nails a {
    text-transform: capitalize;
    color: rgb(231, 66, 94);
    color: var(--clr-primary-2);
    font-weight: bold;
    letter-spacing: var(--spacing);
    transition: var(--transition);
    font-family: 'roboto';
  }
  .nav-links-nails a:hover {
    color: yellow;
  }
  .nav-center-nails {
    display: grid;
    grid-template-columns: auto 1fr;
    align-items: center;
  }
}
